// Layout
// ============
// Major layout rules to go here

body, html {
    overflow-x: hidden;

    &.modal-active {
        position: fixed;
        overflow: hidden;
    }
}
* {
    -webkit-tap-highlight-color: rgba(0,0,0,0); 
    -webkit-backface-visibility: hidden; 
    backface-visibility: hidden;
}